header.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 36px 15px;
}
header.header nav.navigation {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
header.header nav.navigation ul {
  list-style-type: none;
  display: flex;
  gap: 30px;
}
header.header nav.navigation ul li a {
  font-size: 1.625em;
  color: #FFFFFF;
}
header.header div.logo {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
header.header div.logo a {
  display: block;
}
header.header div.logo a img {
  height: 130px;
}
header.header div.access {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
header.header div.access ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  gap: 30px;
}
header.header div.access ul > li a {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
header.header div.access ul > li a img {
  width: 44px;
  height: 36px;
}
header.header div.access ul > li a span {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 0.9375em;
  color: #FFFFFF;
}

@media only screen and (min-width: 992px) {
  header.header nav.navigation div.hamburger {
    display: none;
  }
}
header.header div.access ul > li a {
  align-items: center;
}

header.header div.access ul > li.bag a img {
  width: 35px;
}

@media only screen and (max-width: 992px) {
  header.header {
    padding: 20px 15px;
  }
  header.header nav.navigation div.hamburger a {
    display: flex;
    flex-direction: column;
  }
  header.header nav.navigation div.hamburger a span {
    font-family: "Montserrat", sans-serif;
    text-align: center;
  }
  header.header nav.navigation ul {
    display: none;
  }
  header.header div.access ul {
    gap: 15px;
  }
  header.header div.access ul > li a img {
    width: 30px;
    height: 30px;
  }
  header.header nav.navigation div.hamburger a img {
    width: 40px;
    height: 40px;
  }
  header.header nav.navigation div.hamburger a,
  header.header div.access ul > li a {
    align-items: center;
  }
  header.header nav.navigation div.hamburger a span,
  header.header div.access ul > li a span {
    font-size: 0.75em;
    color: #FFFFFF;
  }
  header.header nav.navigation div.hamburger a img {
    width: 30px;
    height: 30px;
  }
  header.header nav.navigation {
    width: 65%;
  }
  header.header nav.navigation {
    position: relative;
  }
  header.header nav.navigation ul {
    position: absolute;
    left: 0;
    top: 50px;
    background-color: #fff;
    width: 50vw;
    padding: 15px;
    border-radius: 3px;
    line-height: 2em;
  }
  header.header nav.navigation ul.show {
    display: block;
  }
  header.header nav.navigation ul li a {
    color: #000;
    font-size: 1.125em;
  }
}
section.main-highlight {
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: -215px;
  background-position: bottom;
}
section.main-highlight div.wrap-highlight {
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
section.main-highlight div.wrap-highlight div.featured-products {
  width: 100%;
