  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  margin-top: 107.5px;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
}
