  padding-left: 50px;
  text-align: left;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-buy {
  margin-left: -120px;
  width: calc(100% + 120px);
  text-align: right;
  font-weight: bold;
}
section.main-highlight div.wrap-highlight div.featured-products div.item-highlight.highlight-right div.highlight-info a.info-view {
  padding-right: 50px;
  text-align: right;
}

